body{
    overflow: hidden;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background-color: rgb(255, 205, 205);
    user-select: none;
}
input{
    user-select: none;
}
.game-box{
    position: relative;
    min-height: 250px;
    max-height: 250px;
    border: solid 2px black;
    padding: 10px;
    overflow: hidden;
}
.hiden{
    display: none;
}
.game-started{
    opacity: 0.3;
}
.game-box__text.active{
    position: absolute;
    top: 10px;
    left: 10px;
}
.tab-box{
    max-width: 1000px;
    margin: 0 auto;
    display: grid;    
    gap: 15px;
    grid-template-columns: 1fr  300px;
}
.time-box{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

}
.time-box.active{
    color: rgb(238, 24, 24);

}
.time-box__icon{
    margin-bottom: -6px;
}
.tab-last-best{
    display:flex;
    gap: 100px;
    text-wrap: nowrap;
}
.login-box{
    display:flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    
}
.login-form{
    display: flex;
    flex-direction: column;
    gap: 30px;
    border: solid 1px red;
    border-radius: 10px;
    padding: 30px;
}
.login-form__box{
    display: flex;
    gap: 30px;
}
.login-form__title{
    padding: 0;
    margin: 0;
    text-align: center;
}